* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav {
  margin: 0 50px;
  display: flex;
}

.logo__link {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  white-space: nowrap;
  color: #000;
  height: 60px;
  font-family: Roboto, sans-serif;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.nav__link {
  color: #000;
  text-align: left;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__item:not(:last-child :first-child) {
  margin-left: 20px;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  cursor: default;
  border-bottom: 4px solid #00acdc;
  border-radius: 8px;
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: #00acdc;
}
